import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { selectCity, searchCity, enterCity, fetchWeatherIfNeeded, invalidateWeather } from '../actions'
import '../style/weather.css'
// import cityData from '../images/city.list.json'
import FiveDay from '../components/FiveDay'


class App extends Component {
    static propTypes = {
        selectedCity: PropTypes.string.isRequired,
        enteredCity: PropTypes.string.isRequired,
        weathers: PropTypes.array.isRequired,
        cityList: PropTypes.array.isRequired,
        isFetching: PropTypes.bool.isRequired,
        lastUpdated: PropTypes.number,
        getCityName: PropTypes.string.isRequired,
        getCountryName: PropTypes.string.isRequired,
        dispatch: PropTypes.func.isRequired
    }

    componentDidMount() {
        const {dispatch, selectedCity} = this.props;
        dispatch(fetchWeatherIfNeeded(selectedCity));
    }

    componentDidUpdate(prevProps) {
        if(prevProps.selectedCity !== this.props.selectedCity){
            const {dispatch, selectedCity} = this.props;
            dispatch(fetchWeatherIfNeeded(selectedCity));
        }
    }

    handleClick(e){
        console.log(e.target.value);
        this.props.dispatch(selectCity(e.target.value));     
    }

    handleSubmit(e){
        e.preventDefault();
        const {dispatch, enteredCity} = this.props;
        dispatch(searchCity(enteredCity));
        dispatch(fetchWeatherIfNeeded(this.props.selectedCity));
    }

    handleChange(e) {
        this.props.dispatch(enterCity(e.target.value));
    }

    handleRefresh = (e) => {
        e.preventDefault();
        const {dispatch, selectedCity} = this.props;
        dispatch(invalidateWeather(selectedCity));
        dispatch(fetchWeatherIfNeeded(selectedCity)); 
    }

    render(){
        const {weathers, getCityName, getCountryName, cityList, lastUpdated, isFetching} = this.props;
        const navBars = [];
        cityList.forEach((c) => {
            navBars.push(<li><button onClick={(e) => this.handleClick(e)} value={c} >{c}</button></li>);
        });
        
        return (
        <div>
            <div className="header-content">
                <nav className="header-nav-wrap">
                        <ul className="header-nav">
                            <li><button>My Weather</button></li>
                            {navBars}
                            <div className="search-container">
                                <form onSubmit={(e) => this.handleSubmit(e)}>
                                <input type="text" value={this.props.enteredCity} onChange={(e) => this.handleChange(e)}></input>
                                <button type="submit"><i className="fa fa-search"></i></button>
                                </form>
                            </div>
                        </ul>
                </nav>
            </div>
            {isFetching ? 
                <i className="loading fa fa-spinner fa-spin fa-3x fa-fw"></i> : 
                <FiveDay weathers={weathers} getCityName={getCityName} getCountryName={getCountryName} lastUpdated={lastUpdated} handleRefresh={this.handleRefresh} />}
            
        </div>
        
        );
    }
}

const mapStateToProps = state => {
    const {selectedCity, enteredCity, weatherByCity} = state;
    const {isFetching, lastUpdated, items: weathers, getCityName, getCountryName} = weatherByCity[selectedCity] || {isFetching: true, items: [], getCityName: '', getCountryName: ''};
    const cityList = Object.keys(weatherByCity);


    return {
        selectedCity,
        enteredCity,
        weathers,
        isFetching,
        lastUpdated,
        getCityName,
        getCountryName,
        cityList
    };
}

export default connect(mapStateToProps)(App)
