import React from 'react'
import PropTypes from 'prop-types'
import Card from './Card'

const FiveDay = ({weathers, getCityName, getCountryName, lastUpdated, handleRefresh}) => (
    <div className="weather-large">
        <div className="weather-header">
            <h1>{getCityName}, {getCountryName} </h1>
            <span>Last Updated: {new Date(lastUpdated).toLocaleTimeString()}</span>
            <button className="refresh" onClick={(e) => handleRefresh(e)}> <i className="fa fa-refresh fa-spin fa-2x fa-fw"></i></button>
        </div>
        <div className="weather-wrapper">
            {weathers.map(w => (<Card dt={w.dt} temp={w.temp} cond={w.cond} />))}
        </div>
    </div>
   
)

FiveDay.protoTypes = {
    weathers: PropTypes.array.isRequired,
    getCityName: PropTypes.string.isRequired,
    getCountryName: PropTypes.string.isRequired,
    handleRefresh: PropTypes.func.isRequired
}

export default FiveDay