import { combineReducers } from 'redux'
import {
  SEARCH_CITY, SELECT_CITY, ENTER_CITY, INVALIDATE_WEATHER, RECEIVE_WEATHER, REQUEST_WEATHER
} from '../actions'

const selectedCity = (state = 'ann arbor', action) => {
    switch (action.type) {
        case SELECT_CITY:
        case SEARCH_CITY:
            return action.cityName
        default:
            return state
    }
}

const enteredCity = (state = '', action) => {
    switch (action.type) {
        case ENTER_CITY:
            return action.content.toLowerCase()
        default:
            return state
    }
}

const weathers = (state = {isFetching: false, didInvalidate: false, items:[]}, action) => {
    switch (action.type) {
        case INVALIDATE_WEATHER:
            return {
                ...state,
                didInvalidate: true
            }
        case REQUEST_WEATHER:
            return {
                ...state,
                didInvalidate: false,
                isFetching: true
            }
        case RECEIVE_WEATHER:
            return {
                ...state,
                didInvalidate: false,
                isFetching: false,
                lastUpdated: action.receivedAt,
                items: action.weathers,
                getCityName: action.getCityName,
                getCountryName: action.getCountryName
            }
        default:
            return state
    }
}

const weatherByCity = (state = { }, action) => {
    switch (action.type) {
        case INVALIDATE_WEATHER:
        case REQUEST_WEATHER:
        case RECEIVE_WEATHER:
            return {
                ...state,
                [action.cityName]: weathers(state[action.cityName], action)
            }
        default:
            return state
    }
}

const rootReducer = combineReducers({
    selectedCity,
    enteredCity,
    weatherByCity
})

export default rootReducer
