export const REQUEST_WEATHER = 'REQUEST_WEATHER'
export const RECEIVE_WEATHER = 'RECEIVE_WEATHER'
export const SELECT_CITY = 'SELECT_CITY'
export const SEARCH_CITY = 'SEARCH_CITY'
export const ENTER_CITY = 'ENTER_CITY'
export const INVALIDATE_WEATHER = 'INVALIDATE_WEATHER'

export const selectCity = cityName => ({
    type: SELECT_CITY,
    cityName
})

export const searchCity = cityName => ({
    type: SEARCH_CITY,
    cityName
})

export const enterCity = content => ({
    type: ENTER_CITY,
    content
})

export const invalidateWeather = cityName => ({
    type: INVALIDATE_WEATHER,
    cityName
})

export const requestWeather = cityName => ({
    type: REQUEST_WEATHER,
    cityName
})

function wIcon(cond){
    let result;
    if( cond >= 200 && cond < 300 ){
        result = "thunder";
    }else if(cond < 500){
        result = "rainy-4";
    }else if(cond < 600){
        result = "rainy-6";
    }else if(cond < 700){
        result = "snowy-5";
    }else if(cond < 800){
        result = "cloudy-day-1";
    }else if(cond === 800){
        result = "day";
    }else{
        result = "cloudy";
    }
    return result;
}

function getWeathers(data) {
    const options = { weekday: 'short', month: 'short', day: '2-digit' };
    let five = [];
    for(let i in data){
        if(i % 8 === 0){
            let one = {};
            let current = data[i];
            let dt = new Date(current.dt * 1000);
            one["dt"] = dt.toLocaleString('en-US', options);
            one["temp"] = Math.round(current.main.temp);
            one["cond"] = wIcon(current.weather[0].id);
            five.push(one);
        }
    }
    return five;
}

export const receiveWeather = (cityName, json) => ({
    type: RECEIVE_WEATHER,
    cityName,
    weathers: getWeathers(json.list),
    getCityName: json.city.name,
    getCountryName: json.city.country,
    receivedAt: Date.now()
})

const fetchWeather = cityName => dispatch => {
    dispatch(requestWeather);
    return fetch(`https://api.openweathermap.org/data/2.5/forecast?units=metric&q=${cityName}&appid=312b1bac995abc93b80f070e12bc0b95`)
        .then(response => response.json())
        .then(json => { dispatch(receiveWeather(cityName, json)) })
}

